import { Routes, RouterModule } from '@angular/router';
import { ReportsHomeComponent } from './reports-home/reports-home.component';
import { ReimburesementClaimComponent } from './reimburesement-claim/reimburesement-claim.component';
import { FlexiClaimFormComponent } from './flexi-claim-form/flexi-claim-form.component';
import { ViewPlaySlipComponent } from './view-play-slip/view-play-slip.component';
import { TaxCalculatorComponent } from './tax-calculator/tax-calculator.component';
import { ViewTaxSlipComponent } from './view-tax-slip/view-tax-slip.component';
import { ViewTaxPlannerComponent } from './view-tax-planner/view-tax-planner.component';
import { IncomeTaxDetailsSubmissionComponent } from './income-tax-details-submission/income-tax-details-submission.component';
import { ReimbursmentSubmissionComponent } from './reimbursment-submission/reimbursment-submission.component';
import { MiscItemsDocumentsComponent } from './misc-items-documents/misc-items-documents.component';
import { ViewFormSixteenComponent } from './view-form-sixteen/view-form-sixteen.component';

const REPORTS_ROUTES: Routes = [
    {
        path: '',
        component: ReportsHomeComponent
    },
    {
        path: 'reimbursement-claim',
        component: ReimburesementClaimComponent
    },
    {
        path: 'flexi-claim',
        component: FlexiClaimFormComponent
    },
    {
        path: 'view-play-slip',
        component: ViewPlaySlipComponent
    },
    {
        path: 'view-tax-slip',
        component: ViewTaxSlipComponent
    },
    {
        path: 'view-tax-planner',
        component: ViewTaxPlannerComponent
    },
    {
        path: 'income-tax-details-submission',
        component: IncomeTaxDetailsSubmissionComponent
    },
    {
        path: 'reimbursment-submission',
        component: ReimbursmentSubmissionComponent
    },
    {
        path: 'tax-calculator',
        component: TaxCalculatorComponent
    },
    {
        path: 'misc-items-documents',
        component: MiscItemsDocumentsComponent
    }, {
        path: 'view-form-sixteen',
        component: ViewFormSixteenComponent
    }
];

export const ReportsRouter = RouterModule.forChild(REPORTS_ROUTES);
